import React from 'react';
import s from './Main.module.scss'
import myPhoto from '../assets/image/IAm.jpg'
import Typewriter from 'typewriter-effect';
import { Fade } from 'react-awesome-reveal';
import {Particle} from "../common/components/particles/Particle";

export const Main = () => {
    const mainPhoto = {
        backgroundImage: 'url(' + myPhoto + ')',
    };

    return (
        <div className={s.mainPage}>
            <Particle />
            <div className={s.mainBlock}>
                <div className={s.text}>
                    <Fade>
                        <p>I enter first...</p>
                        <p>...then comes my turn...</p>
                        <p>...and finally you see me!</p>
                    </Fade>
                    <h6 className={s.name}>
                        <span className={s.name_1}>I Am  </span>
                        <span>ANDREY PATS</span>
                       </h6>
                    <div className={s.profession}>
                        <Typewriter
                            options={{
                                autoStart: true,
                                loop: true,
                                delay: 50,
                            }}
                            onInit={(typewriter) => {
                                typewriter
                                    .typeString('A FRONTEND  DEVELOPER')
                                    .pauseFor(1000)
                                    .deleteAll()
                                    .pauseFor(1000)
                                    .start();
                            }}
                        />
                    </div>
                </div>
                <div className={s.photo} style={mainPhoto}></div>
            </div>
        </div>
    );
};